const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

// variables
let timerId = null;
let currentColor = '';
const refs = {
  body: document.querySelector('body'),
  btnStart: document.querySelector("[data-action='start']"),
  btnStop: document.querySelector("[data-action='stop']"),
};
// events
refs.btnStart.addEventListener('click', handleBtnStart);
refs.btnStop.addEventListener('click', handleBtnStop);

// functions
const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

const randomColor = colorArray => {
  let randomInteger = randomIntegerFromInterval(0, colors.length - 1);
  let randomColor = `${colorArray[randomInteger]}`;
  return randomColor;
};

function handleBtnStart() {
  refs.btnStart.setAttribute('disabled', 'disabled');
  timerId = setInterval(() => {
    let new_color = `${randomColor(colors)}`;
    while (new_color === currentColor) {
      new_color = `${randomColor(colors)}`;
    }
    refs.body.style.backgroundColor = new_color;
    currentColor = new_color;
  }, 1000);
}

function handleBtnStop() {
  refs.btnStart.removeAttribute('disabled', 'disabled');
  clearInterval(timerId);
}
